  background: rgb(255, 255, 255);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 12px;
}

#total-score-rank {
  width: 60%;
  height: 35px;
  border: none;
  color: #3695be;
  font-weight: 600;
  background: #cfeaf7;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 12px;
  margin-top: -0.5rem;
  margin-bottom: 1.5rem;
}

#total-score-rank:hover {
  width: 60%;
  height: 35px;
  border: none;
  color: #3695be;
  border: solid 2px #3695be;
  font-weight: 600;
  background: rgb(255, 255, 255);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 12px;
  margin-top: -0.5rem;
  margin-bottom: 1.5rem;
}

#wearable-middle-left {
  width: 35%;
  height: 100%;
  /* display: flex;
  justify-content: center;
  align-items: center; */
}
#wearable-middle-left-down {
  /* width: 35%;
  height: 100%; */
  margin-top: 0.5rem;
  margin-left: 4rem;
  display: flex;
  justify-content: center;
  align-items: end;
}
#wearable-profile-img-avatar {
  background-color: #ace3fa;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 50px;
  margin-right: 1rem;
}
/* #wearable-profile-info {
  background-color: #ace3fa;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 12px;
} */

#wearable-middle {
  width: 100%;
  height: 330px;
  display: flex;
}

#wearable-middle-right {
  width: 65%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 4rem;
  margin-top: 0.5rem;
}
.wearable-profile-info-p {
  margin: 0.5rem;
  font-size: 18px;
  font-weight: 600;
  color: #172176;
}
#wearable-middle-right-div {
  width: 100%;
  height: 90%;
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
#wearable-middle-right-div-left {
  width: 40%;
  height: 90%;
}
#wearable-middle-right-div-right {
  width: 60%;
  height: 90%;
}
#WearablePastTotal {
  margin-top: -1rem;
  width: 95%;
}
#wearable-footer {
  margin-top: 8rem;
}
</style>